.loginContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}
.loginForm{
    display: flex;
    flex-direction: column;
    border: 2px solid #ccc;
    width: 450px;
    height: 400px;
    padding: 20px;
    border-radius: 15px;
}
.loginHeaderDiv{
    text-align: center;
}
.formGroup{
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}
.formGroup>input{
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 3px;
    background-color: #f8f8f8;
    font-size: 24px;
}
.rememberMeDiv{
    display: flex;
    flex-direction: row;
}
.rememberMeDiv>input{
    display: inline-block;
    position: relative;
    cursor: pointer;
}
.loginForm>button{
    display: inline-block;
    padding: 10px 20px;
    font-size: 20px;
    font-weight: bold;
    border: none;
    border-radius: 3px;
    background-color: #000000;
    color: #fff;
    cursor: pointer;
    margin-top: 10px;
}

@media screen and (max-width: 768px) {
    .loginForm{
        width: 300px;
        border: none;
    }
  }